import React from 'react';

import '../../css/header/style.css';
import {dataList} from './data.js';
import List from './list.js';

class Header extends React.Component {

  render () {
    return (
      <div className='main-header'>

        <div className='name-header'>
          <span id='name'>
            <a id='name' href='../../../public/index.html'>The Private Mae Pim</a>
          </span>
        </div>

        <div className='header'>
        {
          dataList.map(data => (
            <List key={data.id} class='normal' link={data.link} tag={data.tag} name={data.name} content={data.content} isShow={data.isShow} img={data.img} />
          ))
        }
        </div>


      </div>
    );
  }

}

export default Header;



