import Navbar from './component/navbar/index.js';
import ImgSlide from './component/imgSlide/index.js';
import ContentCard from './component/contentCard/index.js';
import Footer from './component/footer/index.js';

class App extends Component {
  render() {
    return (
      <div className='the-private'>
        <Header />
        <Navbar />
        <ImgSlide />
        <ContentCard />
        <Footer />
      </div>
    );
  }
}

export default App;



