import React from 'react';
import {imgData} from './imgData.js';

import ImgList from './imgList.js';
import ImgPointer from './imgPointer.js';

import { CSSTransition } from 'react-transition-group';
import '../../css/imgSlide/style.css';

class ImgSlide extends React.Component {

  state = {
    pointer: 1
  }

  setPointer=(num)=> {
    this.setState(state => ({
      pointer: num
    }))
  }

  componentDidMount() {
    this.timerID = setInterval(() => this.autoSlide(), 7000);
  }

  componentDidUpdate() {
    this.timerID = setInterval(() => this.autoSlide(), 7000);
  }

  UNSAFE_componentWillUpdate(nextProps, nextState) {
    clearInterval(this.timerID);
  }

  autoSlide() {
    this.setState(state => ({
      pointer: state.pointer < imgData.length ? state.pointer + 1 : 1
    }))
  }

  render() {
    //console.log(this.state.num);
    const pointer = this.state.pointer;
    return (
      <div className='img-slide'>
      <br/>
        <div className='img-list'>
            {
              imgData
              .filter(img => (img.id === pointer))
              .map(img => (
                <CSSTransition
                  key={img.id}
                  in={true}
                  appear={true}
                  timeout={2000}
                  classNames="fade"
                  >
                  <ImgList key={img.id} img={img.img}/>
                </CSSTransition>
              ))
            }
        </div>
        <div className='img-pointer'>
        {
          imgData.map(img => (
            <ImgPointer key={img.id} onClick={()=>this.setPointer(img.id)} thisId={img.id} imgId={pointer}/>
          ))
        }
        </div>
      </div>
    );
  }

}

export default ImgSlide;



