import React from 'react';

class List extends React.Component {

  render () {
    //console.log(this.props.link);
    return (
      <span className={this.props.class}>
        <a id={this.props.tag} href={this.props.link} target='_blank'>
          {this.props.content}
          {this.props.isShow && <img className='icon' src={this.props.img} alt={this.props.name}/>}
        </a>
      </span>
    );
  }

}

export default List;



