import React from 'react';
import {dataList} from './dropdata.js';

import List from './list.js';

class Menu extends React.Component {

  render () {
    //console.log(this.props);
    return (
      <span className={this.props.className}>
        {this.props.link !== '' && <a href={this.props.link}>{this.props.content}</a>}
        {this.props.link === '' && <a>{this.props.content}</a>}
        {this.props.isDrop && <i className="fas fa-caret-down"></i>}
        <ul className='content'>
        {this.props.isDrop &&
          dataList.map(data => (
            <List key={data.id} name={data.name} link={data.link}/>
          ))
        }
        </ul>
      </span>
    );
  }

}

export default Menu;



