import React from 'react';

class ImgPointer extends React.Component {

  render () {
    //console.log(this.props.imgId);
    //console.log(this.props.thisId);
    return (
      <span>
        {this.props.imgId !== this.props.thisId && <i className="fas fa-circle" onClick={this.props.onClick} style={{color: 'rgb(200, 200, 200)'}}></i>}
        {this.props.imgId === this.props.thisId && <i className="fas fa-circle" onClick={this.props.onClick} style={{color: 'rgb(0, 191, 255)'}}></i>}
      </span>
    );
  }

}

export default ImgPointer;



