import React from 'react';

import bigQuote from "../../img/contentCard/bigQuote.png";
import bigQuote2 from "../../img/contentCard/bigQuote2.png";

class Card extends React.Component {

  render () {
    const floatPos = ['right', 'left'];
    const pos = this.props.id % 2;
    const boxStyle = {
      float: floatPos[pos],
      //marginTop: '50px'
    };
    //console.log(this.props.id % 2 === 1);
    return (
      <div className='card' id={'card' + this.props.id}>

        <div className='img-box' style={{float: floatPos[pos]}}>
          <img src={this.props.img} alt={this.props.name}/>
        </div>

        <div className='message-box' style={boxStyle}>
          <img id='big-quote' src={bigQuote} alt={'quote-' + this.props.name} />
          <p>
            {this.props.content}
          </p>
          <img id='big-quote2' src={bigQuote2} alt={'quote2-' + this.props.name} />
        </div>

      </div>
    );
  }

}

export default Card;



